import React, { useState } from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, Stars } from '@react-three/drei';
import Planet3D from './Planet3D';

const planets = [
    { name: 'Mercurio', texture: '/NASA_SpaceWeb/textures/mercury.jpg', position: [-6, 0, 0], size: 0.5, description: 'Mercurio es el planeta ms cercano al Sol. Es el ms pequeo del sistema solar y no tiene lunas.' },
    { name: 'Venus', texture: '/NASA_SpaceWeb/textures/venus.jpg', position: [-4, 0, 0], size: 0.9, description: 'Venus es el segundo planeta desde el Sol y es similar en tamao a la Tierra. Es conocido por su atmsfera densa y temperaturas extremas.' },
    { name: 'Tierra', texture: '/NASA_SpaceWeb/textures/earth.jpg', position: [-2, 0, 0], size: 1, description: 'La Tierra es nuestro hogar, el tercer planeta desde el Sol y el nico conocido que alberga vida.' },
    { name: 'Marte', texture: '/NASA_SpaceWeb/textures/mars.jpg', position: [0, 0, 0], size: 0.7, description: 'Marte es conocido como el planeta rojo debido a su superficie rica en xido de hierro. Tiene dos lunas, Fobos y Deimos.' },
    { name: 'Jpiter', texture: '/NASA_SpaceWeb/textures/jupiter.jpg', position: [3, 0, 0], size: 1.8, description: 'Jpiter es el planeta ms grande del sistema solar, conocido por sus bandas de nubes y la Gran Mancha Roja, una tormenta gigante.' },
    { name: 'Saturno', texture: '/NASA_SpaceWeb/textures/saturn.jpg', position: [7, 0, 0], size: 1.6, description: 'Saturno es famoso por sus anillos espectaculares. Es el segundo planeta ms grande del sistema solar.' },
    { name: 'Urano', texture: '/NASA_SpaceWeb/textures/uranus.jpg', position: [10, 0, 0], size: 1.2, description: 'Urano es un gigante de hielo conocido por su inclinacin axial extrema, lo que hace que "ruede" en su rbita alrededor del Sol.' },
    { name: 'Neptuno', texture: '/NASA_SpaceWeb/textures/neptune.jpg', position: [13, 0, 0], size: 1.1, description: 'Neptuno es el planeta ms alejado del Sol. Es un gigante de hielo conocido por sus vientos extremadamente rpidos.' },
  ];

function SolarSystem() {
  const [selectedPlanet, setSelectedPlanet] = useState(null);

  return (
    <div style={{ display: 'flex' }}>
      <Canvas style={{ height: '70vh', background: 'black', flex:4 }}>
        <ambientLight intensity={0.5} />
        <pointLight position={[10, 10, 10]} />
        <Stars />

        {planets.map((planet, index) => (
          <Planet3D
            key={index}
            texture={planet.texture}
            position={planet.position}
            size={planet.size}
            onClick={() => setSelectedPlanet(planet)}
          />
        ))}

        <OrbitControls enableZoom={true} />
      </Canvas>
      
      {selectedPlanet && (
        <div style={{ padding: '20px', backgroundColor: 'white', textAlign: 'center', flex:1 }}>
          <h2>{selectedPlanet.name}</h2>
          <p>{selectedPlanet.description}</p>
        </div>
      )}
    </div>
  );
}

export default SolarSystem;
